.app.dark {
	background-color: #111 !important;
	color: rgb(156, 156, 156);
	.homContainer {
		background-color: #111;
	}
	.chartGrid {
		stroke: rgba(228, 228, 228, 0.219);
	}

	.table {
		background-color: #111;

		.tableCell {
			color: gray;
		}
	}

	.navbar {
		color: #999;
		border-color: #333;

		.searchBar {
			border-color: gray;
		}
	}

	.sideBar {
		background-color: #111;
		border-color: #333;

		.top {
			border-color: #333;
			.logo {
				color: #999;
			}
		}

		ul {
			li {
				span {
					color: #999;
				}

				&:hover {
					background-color: #333;
				}
			}
		}
	}

	.dataTable {
		.dataGrid {
			color: gray;
			border: none;

			.viewButton,
			.deleteButton,
			.cellWithStatus {
				color: gray;
				border: none;
			}
		}
	}

	input {
		background-color: transparent;
	}
}

.app .homContainer {
	background-color: #fff;
}
