.featured {
    flex              : 2;
    box-shadow        : var(--boxShadow);
    -webkit-box-shadow: var(--boxShadowWebKit);
    padding           : 10px;

    .top {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        color          : gray;
        margin-bottom  : 12px;

        .title {
            font-size  : 18px;
            font-weight: 600;
        }
    }

    .bottom {
        padding        : 20px;
        display        : flex;
        flex-direction : column;
        align-items    : center;
        justify-content: center;
        gap            : 15px;

        .featuredChart {
            width : 250px;
            height: 250px;
        }

        .title {
            font-weight: 500;
            color      : gray;
        }

        .amount {
            font-size: 30px;
        }

        .desc {
            font-size  : 16px;
            font-weight: 300;
            color      : gray;
            text-align : center;
        }

        .summary {
            width          : 100%;
            margin-top     : 20px;
            display        : flex;
            align-items    : center;
            justify-content: space-between;

            .item {
                text-align: center;

                .itemTitle {
                    font-size: 14px;
                    color    : gray;
                }

                .itemResult {
                    display    : flex;
                    align-items: center;
                    margin-top : 10px;
                    font-size  : 14px;

                    &.negative {
                        color: red;
                    }

                    &.positive {
                        color: green;
                    }
                }
            }
        }
    }
}